.form-header {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 1rem;
  padding-top: 0.5rem;
  display: block;
}
.controls-above-table {
  display: flex;
  margin-top: 2rem;
  margin-bottom: 1rem;
  justify-content: space-between;
}

.btn-wrap {
  height: 100%;
}

#table {
  display: table;
  width: 100%;
  max-width: 100%;
  border-spacing: 2px;
  border-color: gray;
}
#table thead th {
  display: table-cell;
  /* font-size: 0.6rem; */
  border-top: none;
  border-bottom: 1px solid #999;
  padding: 0.75rem;
  text-align: left;
  font-weight: 700;
}

#table tr {
  transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

#table tr:hover {
  background: rgba(94, 130, 152, 0.05);
}

#table td {
  border-top: 1px solid rgba(83, 101, 140, 0.08);
  padding: 0.7rem 0.5rem;
}

@media screen and (min-width: 1100px) {
  .table {
    overflow: visible;
  }
}
